// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

// 暂时移除vue-toastification，因为它可能与Vue 2不兼容
// import Toast from 'vue-toastification'
// import 'vue-toastification/dist/index.css'
// app.use(Toast)

/* 路由 */
import router from './router'

/* axios */
import axios from './api'
import api from './api/api'
Vue.prototype.http = axios;
Vue.prototype.api = api;

/* swiper */
import 'swiper/dist/css/swiper.min.css';

/* 重置样式 */
import './assets/css/reset.min.css'

/* jquery */
import 'jquery'

/* bootstarp */
import './assets/css/bootstrap.min.css'
import './assets/js/bootstrap.min'

/* animate.css */
import 'animate.css'

 
/* 头部组件 */
import Header from './components/Header'
Vue.component(Header.name,Header)


/* 尾部组件 */
import Footer from './components/Footer'
Vue.component(Footer.name,Footer)

/* 回到顶部 */
import GoTop from './components/GoTop'
Vue.component(GoTop.name,GoTop)

Vue.config.productionTip = false


router.beforeEach((to, from, next) => {
    if(to.meta.title){
      document.title = to.meta.title
    }
    next();
});

// 路由切换后记录访问统计
router.afterEach((to, from) => {
    // 延迟执行，确保页面已经渲染
    setTimeout(() => {
        recordPageVisit(to);
    }, 100);
});

// 记录页面访问的函数
function recordPageVisit(route) {
    try {
        // 只记录主要页面的访问，排除重复和无意义的路由
        if (shouldRecordPageVisit(route)) {
            axios.post('http://localhost:8081/admin/api/record/visit', {}, {
                timeout: 5000,
                headers: {
                    'Content-Type': 'application/json'
                }
            }).then(response => {
                console.log('页面访问统计记录成功:', route.path);
            }).catch(error => {
                // 静默处理错误，不影响用户体验
                console.warn('页面访问统计记录失败:', error.message);
            });
        }
    } catch (error) {
        console.warn('记录页面访问时发生错误:', error);
    }
}

// 判断是否应该记录页面访问
function shouldRecordPageVisit(route) {
    if (!route || !route.path) {
        return false;
    }
    
    // 排除重定向路由
    if (route.path === '/' && route.redirectedFrom) {
        return false;
    }
    
    // 只记录主要页面
    const validPaths = [
        '/home',
        '/bussiness',
        '/bussiness/zhimingzhePlatform', 
        '/bussiness/zhimingzheApp',
        '/service',
        '/zhimingzheBigPlatform',
        '/newsinformation',
        '/companyintroduction',
        '/jobchance',
        '/contactus',
        '/servicedetail'
    ];
    
    return validPaths.includes(route.path) || route.path.startsWith('/expertTeamDetail/');
}


// 处理vue-router重复导航错误
import Router from 'vue-router'
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => {
    if (err.name !== 'NavigationDuplicated') throw err
  })
}

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



