<template>
  <!-- 头部整体盒子 -->
  <div id="header" class="container-fluid">
    <div class="header-main container">
      <!-- logo和公司名 -->
      <div class="header-logo-title">
        <img class="header-logo-img" src="@/assets/img/zhimingzhelogo.png" alt="logo">
        <div class="header-title-group">
          <div class="header-title-cn">知命者文化产业有限公司</div>
          <div class="header-title-en">ZHIMINGZHE CULTURE INDUSTRY CO., LTD.</div>
        </div>
      </div>
      <!-- 导航内容 -->
      <ul class="header-nav-wrapper">
        <li
          v-for="(item,index) in navList"
          :key="index"
          :class="[index==navIndex?'active':'', item.children.length>0?'has-dropdown':'']"
          @mouseenter="showDropdown(index)"
          @mouseleave="hideDropdownWithDelay(index)"
        >
          <router-link :to="{path: item.path, query: {scrollTop: 0}}">
            {{item.name}}
            <span v-if="item.children.length>0" class="glyphicon glyphicon-menu-down"></span>
            <i class="underline"></i>
          </router-link>
          <dl v-if="item.children.length>0" :class="{'show-dropdown': activeDropdown === index}">
            <router-link
              v-for="(i,n) in item.children"
              :key="n"
              :to="{path: i.path, query: {scrollTop: 0}}"
              class="dropdown-link"
              @click.native.stop="handleChildClick(index, item.name, i.path)"
            >
              <dt>
                {{i.name}}
              </dt>
            </router-link>
          </dl>
        </li>
      </ul>
    </div>
    
    <!-- 手机端头部 -->
    <div class="header-mobile visible-xs">
      <div class="mobile-header-content">
        <!-- 左侧logo -->
        <div class="mobile-logo" @click="goHome">
          <img src="@/assets/img/zhimingzhelogo.png" alt="logo">
          <div class="mobile-title">
            <div class="mobile-title-cn">知命者文化</div>
            <div class="mobile-title-en">ZHIMINGZHE</div>
          </div>
        </div>
        
        <!-- 右侧汉堡菜单按钮 -->
        <div class="hamburger-menu" @click="toggleSidebar" :class="{ active: sidebarOpen }">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
    
    <!-- 侧栏导航 -->
    <div class="sidebar-overlay" :class="{ active: sidebarOpen }" @click="closeSidebar"></div>
    <div class="sidebar-nav" :class="{ active: sidebarOpen }">
      <div class="sidebar-header">
        <div class="sidebar-logo">
          <img src="@/assets/img/zhimingzhelogo.png" alt="logo">
          <div class="sidebar-title">
            <div class="sidebar-title-cn">知命者文化产业有限公司</div>
            <div class="sidebar-title-en">ZHIMINGZHE CULTURE INDUSTRY CO., LTD.</div>
          </div>
        </div>
        <div class="close-btn" @click="closeSidebar">
          <span class="glyphicon glyphicon-remove"></span>
        </div>
      </div>
      
      <div class="sidebar-content">
        <ul class="sidebar-nav-list">
          <li
            v-for="(item, index) in navList"
            :key="index"
            :class="{ active: index == navIndex, 'has-children': item.children.length > 0 }"
          >
            <div class="nav-item">
              <div 
                v-if="item.children.length > 0" 
                class="nav-link"
                @click.stop="handleSidebarNavClick(item, index)"
              >
                {{ item.name }}
              </div>
              <router-link 
                v-else 
                :to="{ path: item.path, query: { scrollTop: 0 } }" 
                @click.native="closeSidebar"
              >
                {{ item.name }}
              </router-link>
              <span 
                v-if="item.children.length > 0" 
                class="expand-icon"
                :class="{ expanded: expandedItems.includes(index) }"
                @click.stop="toggleExpand(index)"
              >
                <span class="glyphicon glyphicon-chevron-down"></span>
              </span>
            </div>
            
            <!-- 子菜单 -->
            <ul v-if="item.children.length > 0" class="sidebar-submenu" :class="{ expanded: expandedItems.includes(index) }">
              <li v-for="(child, childIndex) in item.children" :key="childIndex">
                <router-link 
                  :to="{ path: child.path, query: { scrollTop: 0 } }" 
                  @click.native.stop="handleChildNavClick(index, item.name, child.path)"
                >
                  {{ child.name }}
                </router-link>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      navIndex: sessionStorage.getItem('navIndex') ? sessionStorage.getItem('navIndex') : 0,
      menuName: "首页",
      menuClass: "glyphicon glyphicon-menu-down",
      activeDropdown: null,
      dropdownTimer: null,
      sidebarOpen: false,
      expandedItems: [],
      isNavigating: false, // 添加导航状态标记
      navigationTimer: null, // 添加导航防抖定时器
      isToggling: false, // 添加切换状态标记
      navList: [
        {
          name: "首页",
          path: "/",
          children: []
        },
        {
          name: "公司介绍",
          path: "/companyintroduction",
          children: []
        },
        {
          name: "业务介绍",
          path: "/bussiness",
          children: [
            {
              name: "知命者平台",
              path: "/bussiness/zhimingzhePlatform"
            },
            {
              name: "知命者智能程式",
              path: "/bussiness/zhimingzheApp"
            }
          ]
        },
        {
          name: "公司服务",
          path: "/service",
          children: []
        },
        {
          name: "最新动态",
          path: "/newsinformation",
          children: []
        },
        {
          name: "合作伙伴",
          path: "/jobchance",
          children: []
        },
        {
          name: "联系我们",
          path: "/contactus",
          children: []
        }
      ]
    };
  },
  methods: {
    // 切换侧栏显示/隐藏
    toggleSidebar() {
      this.sidebarOpen = !this.sidebarOpen;
      if (this.sidebarOpen) {
        document.body.style.overflow = 'hidden';
      } else {
        document.body.style.overflow = '';
      }
    },
    
    // 关闭侧栏
    closeSidebar() {
      this.sidebarOpen = false;
      document.body.style.overflow = '';
    },
    
    // 切换子菜单展开/收起
    toggleExpand(index) {
      // 防止快速点击
      if (this.isToggling) return;
      
      this.isToggling = true;
      const expandIndex = this.expandedItems.indexOf(index);
      
      if (expandIndex > -1) {
        this.expandedItems.splice(expandIndex, 1);
      } else {
        // 关闭其他展开的菜单
        this.expandedItems = [index];
      }
      
      // 重置切换状态
      setTimeout(() => {
        this.isToggling = false;
      }, 300);
    },
    
    // 处理侧栏导航点击
    handleSidebarNavClick(item, index) {
      if (item.children.length > 0) {
        // 如果有子菜单，只进行展开/收起操作
        this.toggleExpand(index);
        return;
      }
    },
    
    // 处理子导航点击
    handleChildNavClick(parentIndex, parentName, childPath) {
      // 防止重复导航
      if (this.$route.path === childPath) {
        this.closeSidebar();
        return;
      }
      
      // 先关闭侧栏
      this.closeSidebar();
      
      // 更新导航状态
      this.navIndex = parentIndex;
      sessionStorage.setItem('navIndex', parentIndex);
      this.menuName = parentName;
      
      // 使用 nextTick 确保 DOM 更新后再进行导航
      this.$nextTick(() => {
        this.$router.push({
          path: childPath,
          query: { scrollTop: 0 }
        }).then(() => {
          window.scrollTo(0, 0);
        }).catch(err => {
          // 忽略导航取消错误
          if (err.name !== 'NavigationDuplicated' && err.name !== 'NavigationCancelled') {
            console.error('Navigation error:', err);
          }
        });
      });
    },
    
    navClick(index, name, path) {
      // 防止重复导航和同时进行多个导航
      if (this.$route.path === path || this.isNavigating) {
        return;
      }
      
      // 清除之前的导航定时器
      if (this.navigationTimer) {
        clearTimeout(this.navigationTimer);
      }
      
      // 防抖处理，避免快速点击
      this.navigationTimer = setTimeout(() => {
        this.isNavigating = true;
        this.navIndex = index;
        sessionStorage.setItem('navIndex', index);
        this.menuName = name;
        
        // 确保页面从顶部开始显示
        this.$router.push({
          path: path,
          query: { scrollTop: 0 }
        }).then(() => {
          window.scrollTo(0, 0);
          this.isNavigating = false;
        }).catch(err => {
          this.isNavigating = false;
          // 忽略导航取消错误
          if (err.name !== 'NavigationDuplicated') {
            console.error('Navigation error:', err);
          }
        });
      }, 100); // 100ms防抖延迟
    },
    
    // 显示下拉菜单
    showDropdown(index) {
      clearTimeout(this.dropdownTimer);
      this.activeDropdown = index;
    },
    
    // 延迟隐藏下拉菜单
    hideDropdownWithDelay(index) {
      this.dropdownTimer = setTimeout(() => {
        if (this.activeDropdown === index) {
          this.activeDropdown = null;
        }
      }, 300); // 300毫秒延迟
    },
    
    // 处理子菜单点击
    handleChildClick(parentIndex, parentName, childPath) {
      // 防止重复导航
      if (this.$route.path === childPath) {
        this.activeDropdown = null;
        return;
      }
      
      // 阻止事件冒泡，保持下拉菜单打开状态
      clearTimeout(this.dropdownTimer);
      
      this.navIndex = parentIndex;
      sessionStorage.setItem('navIndex', parentIndex);
      this.menuName = parentName;
      
      // 确保页面从顶部开始显示
      this.$router.push({
        path: childPath,
        query: { scrollTop: 0 }
      }).then(() => {
        window.scrollTo(0, 0);
        // 导航完成后再关闭下拉菜单
        setTimeout(() => {
          this.activeDropdown = null;
        }, 100);
      }).catch(err => {
        // 忽略导航取消错误
        if (err.name !== 'NavigationDuplicated') {
          console.error('Navigation error:', err);
        }
        // 即使导航失败也要关闭下拉菜单
        this.activeDropdown = null;
      });
    },
    
    menuClick() {
      if (this.menuClass == "glyphicon glyphicon-menu-down") {
        this.menuClass = "glyphicon glyphicon-menu-up";
      } else {
        this.menuClass = "glyphicon glyphicon-menu-down";
      }
    },
    // 根据当前路由路径更新导航索引
    updateNavIndex() {
      const currentPath = this.$route.path;
      
      // 特殊处理首页路径
      if (currentPath === '/' || currentPath === '/home') {
        this.navIndex = 0; // 首页在navList中的索引为0
        sessionStorage.setItem('navIndex', 0);
        this.menuName = this.navList[0].name;
        return;
      }
      
      const index = this.navList.findIndex(item => {
        // 检查主路径
        if (item.path === currentPath) return true;
        // 检查子路径
        if (item.children && item.children.length > 0) {
          return item.children.some(child => child.path === currentPath);
        }
        return false;
      });
      
      if (index !== -1) {
        this.navIndex = index;
        sessionStorage.setItem('navIndex', index);
        this.menuName = this.navList[index].name;
      }
    },
    
    // 添加跳转到联系我们页面的方法
    goToContact() {
      // 防止重复导航
      if (this.$route.path === '/contactus') {
        return;
      }
      
      const contactIndex = 6; // 联系我们在导航列表中的索引
      this.navIndex = contactIndex;
      sessionStorage.setItem('navIndex', contactIndex);
      this.menuName = "联系我们";
      this.$router.push({
        path: '/contactus',
        query: { scrollTop: 0 }
      }).then(() => {
        window.scrollTo(0, 0);
      }).catch(err => {
        // 忽略导航取消错误
        if (err.name !== 'NavigationDuplicated') {
          console.error('Navigation error:', err);
        }
      });
    },
    
    // 点击logo回到首页
    goHome() {
      // 防止重复导航
      if (this.$route.path === '/') {
        return;
      }
      
      this.navIndex = 0;
      this.menuName = "首页";
      sessionStorage.setItem('navIndex', 0);
      this.$router.push({ path: '/', query: { scrollTop: 0 } }).then(() => {
        window.scrollTo(0, 0);
      }).catch(err => {
        // 忽略导航取消错误
        if (err.name !== 'NavigationDuplicated') {
          console.error('Navigation error:', err);
        }
      });
    },
  },
  watch: {
    // 监听路由变化
    '$route'() {
      this.updateNavIndex();
      // 确保每次路由变化时滚动到顶部
      window.scrollTo(0, 0);
      // 路由变化时关闭侧栏
      this.closeSidebar();
    }
  },
  mounted() {
    // 组件挂载时更新导航索引
    this.updateNavIndex();
    
    // 监听路由完成后的事件，确保滚动到顶部
    this.$router.afterEach(() => {
      window.scrollTo(0, 0);
    });
  },
  
  beforeDestroy() {
    // 组件销毁前恢复body滚动
    document.body.style.overflow = '';
    // 清除定时器
    if (this.dropdownTimer) {
      clearTimeout(this.dropdownTimer);
    }
    if (this.navigationTimer) {
      clearTimeout(this.navigationTimer);
    }
  }
};
</script>

<style scoped>
#header {
  background: #fff;
  box-shadow: none;
  border-bottom: 1.5px solid #eee;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2000;
  padding: 0;
}

.header-main.container {
  display: flex;
  align-items: center;
  height: 90px;
  padding: 0 0 0 10px;
  min-width: 0;
  width: 100%;
  box-sizing: border-box;
}

.header-logo-title {
  display: flex;
  align-items: center;
  margin-right: 40px;
}

.header-logo-img {
  width: 300px;
  height: 80px;
  object-fit: contain;
  margin-right: 16px;
}

.header-title-group {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header-title-cn {
  font-size: 26px;
  font-weight: 700;
  color: #a00000;
  line-height: 1.1;
  letter-spacing: 2px;
}

.header-title-en {
  font-size: 15px;
  color: #666;
  letter-spacing: 1px;
  margin-top: 3px;
}

.header-nav-wrapper {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  flex: 1;
  flex-wrap: nowrap;
  min-width: 0;
}

.header-nav-wrapper > li {
  margin: 0 28px;
  position: relative;
  white-space: nowrap;
}

.header-nav-wrapper > li > a,
.header-nav-wrapper > li > router-link {
  color: #333;
  font-size: 18px;
  font-weight: 500;
  padding: 0 0 6px 0;
  position: relative;
  background: none;
  border: none;
  transition: color 0.2s;
}

.header-nav-wrapper > li.active > a,
.header-nav-wrapper > li.active > router-link,
.header-nav-wrapper > li > a:hover,
.header-nav-wrapper > li > router-link:hover {
  color: #a00000;
  text-decoration: none;
}

.header-nav-wrapper > li.active > a::after,
.header-nav-wrapper > li.active > router-link::after,
.header-nav-wrapper > li > a:hover::after,
.header-nav-wrapper > li > router-link:hover::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #a00000;
  position: absolute;
  left: 0;
  bottom: -2px;
  border-radius: 1px;
}

.header-nav-wrapper > li > a > span {
  font-size: 12px;
  margin-left: 2px;
}

/* 下拉菜单样式 */
.header-nav-wrapper > li > dl {
  display: block;
  position: absolute;
  width: 220px;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 101;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 16px rgba(160,0,0,0.10), 0 1.5px 6px rgba(0,0,0,0.08);
  padding: 8px 0;
  margin-top: 8px;
  transition: all 0.3s ease;
  border: 1px solid #eee;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.header-nav-wrapper > li > dl.show-dropdown {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.header-nav-wrapper > li > dl.show-dropdown .dropdown-link {
  pointer-events: auto;
}

.header-nav-wrapper > li > dl > .dropdown-link {
  display: block;
  text-decoration: none;
}

.header-nav-wrapper > li > dl > .dropdown-link > dt {
  width: calc(100% - 16px);
  padding: 12px 20px;
  border: none;
  cursor: pointer;
  background: transparent;
  font-size: 16px;
  color: #333;
  transition: all 0.2s ease;
  text-align: left;
  border-radius: 6px;
  margin: 4px 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header-nav-wrapper > li > dl > .dropdown-link > dt:hover {
  background: #f7eaea;
  color: #a00000;
  transform: translateX(5px);
}

.header-nav-wrapper > li > dl > .dropdown-link:last-child > dt {
  border-bottom: none;
}

/* 手机端头部样式 */
.header-mobile {
  display: none;
  background: #fff;
  border-bottom: 1.5px solid #eee;
  height: 70px;
  padding: 0 20px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}

.mobile-header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.mobile-logo {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 8px;
  transition: all 0.2s ease;
  margin-left: -12px;
}

.mobile-logo:hover {
  background: rgba(160,0,0,0.05);
}

.mobile-logo img {
  width: 45px;
  height: 45px;
  object-fit: contain;
  margin-right: 12px;
}

.mobile-title-cn {
  font-size: 18px;
  font-weight: 700;
  color: #a00000;
  line-height: 1.1;
  letter-spacing: 1px;
}

.mobile-title-en {
  font-size: 11px;
  color: #666;
  line-height: 1;
  letter-spacing: 0.5px;
  margin-top: 2px;
}

/* 汉堡菜单按钮 */
.hamburger-menu {
  width: 40px;
  height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
  padding: 8px 6px;
  transition: all 0.3s ease;
  border-radius: 8px;
  background: rgba(160,0,0,0.05);
  border: 1px solid rgba(160,0,0,0.1);
}

.hamburger-menu:hover {
  background: rgba(160,0,0,0.1);
  border-color: rgba(160,0,0,0.2);
}

.hamburger-menu span {
  width: 100%;
  height: 3px;
  background: #a00000;
  border-radius: 2px;
  transition: all 0.3s ease;
  transform-origin: center;
}

.hamburger-menu.active span:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
}

.hamburger-menu.active span:nth-child(2) {
  opacity: 0;
}

.hamburger-menu.active span:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -6px);
}

/* 侧栏遮罩层 */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2998;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.sidebar-overlay.active {
  opacity: 1;
  visibility: visible;
}

/* 侧栏导航 */
.sidebar-nav {
  position: fixed;
  top: 0;
  right: -350px;
  width: 350px;
  height: 100%;
  background: #fff;
  z-index: 2999;
  transition: all 0.3s ease;
  box-shadow: -2px 0 10px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

.sidebar-nav.active {
  right: 0;
}

/* 侧栏头部 */
.sidebar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid #eee;
  background: #f8f9fa;
}

.sidebar-logo {
  display: flex;
  align-items: center;
  flex: 1;
}

.sidebar-logo img {
  width: 40px;
  height: 40px;
  object-fit: contain;
  margin-right: 12px;
}

.sidebar-title-cn {
  font-size: 16px;
  font-weight: 700;
  color: #a00000;
  line-height: 1.2;
}

.sidebar-title-en {
  font-size: 10px;
  color: #666;
  line-height: 1;
  margin-top: 2px;
}

.close-btn {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 50%;
  transition: background 0.2s ease;
}

.close-btn:hover {
  background: #e9ecef;
}

.close-btn .glyphicon {
  font-size: 16px;
  color: #666;
}

/* 侧栏内容 */
.sidebar-content {
  padding: 0;
}

.sidebar-nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.sidebar-nav-list > li {
  border-bottom: 1px solid #f0f0f0;
}

.nav-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  position: relative;
}

.nav-item > a,
.nav-item > .nav-link {
  flex: 1;
  padding: 18px 20px;
  color: #333;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.2s ease;
  display: block;
  cursor: pointer;
}

.nav-item > a:hover,
.nav-item > .nav-link:hover {
  color: #a00000;
  background: #f8f9fa;
  text-decoration: none;
}

.sidebar-nav-list > li.active > .nav-item > a,
.sidebar-nav-list > li.active > .nav-item > .nav-link {
  color: #a00000;
  background: #f8f9fa;
  border-left: 4px solid #a00000;
}

.expand-icon {
  width: 40px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
}

.expand-icon:hover {
  background: #f0f0f0;
}

.expand-icon .glyphicon {
  font-size: 12px;
  color: #666;
  transition: transform 0.3s ease-in-out;
  transform: rotate(0deg);
}

.expand-icon.expanded .glyphicon {
  transform: rotate(180deg);
}

/* 子菜单 */
.sidebar-submenu {
  list-style: none;
  margin: 0;
  padding: 0;
  background: #f8f9fa;
  max-height: 0;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  transform: translateY(-10px);
}

.sidebar-submenu.expanded {
  max-height: 500px;
  opacity: 1;
  transform: translateY(0);
}

.sidebar-submenu li {
  border-bottom: 1px solid #e9ecef;
}

.sidebar-submenu li:last-child {
  border-bottom: none;
}

.sidebar-submenu a {
  display: block;
  padding: 15px 20px 15px 40px;
  color: #555;
  font-size: 14px;
  text-decoration: none;
  transition: all 0.2s ease;
  position: relative;
}

.sidebar-submenu a:hover {
  color: #a00000;
  background: #e9ecef;
  text-decoration: none;
  padding-left: 45px;
}

.sidebar-submenu a::before {
  content: "•";
  position: absolute;
  left: 25px;
  color: #999;
  font-size: 16px;
}

/* 响应式样式 */
@media screen and (max-width: 997px) {
  .header-main.container {
    display: none;
  }
  
  .header-mobile {
    display: block !important;
  }
}

@media screen and (min-width: 998px) {
  .header-mobile {
    display: none !important;
  }
  
  .sidebar-nav,
  .sidebar-overlay {
    display: none !important;
  }
}

/* 中等屏幕优化 */
@media screen and (max-width: 600px) {
  .header-mobile {
    height: 65px;
    padding: 0 18px;
  }
  
  .mobile-logo img {
    width: 42px;
    height: 42px;
  }
  
  .mobile-title-cn {
    font-size: 16px;
  }
  
  .mobile-title-en {
    font-size: 10px;
  }
  
  .hamburger-menu {
    width: 38px;
    height: 38px;
  }
}

/* 小屏幕优化 */
@media screen and (max-width: 480px) {
  .header-mobile {
    height: 60px;
    padding: 0 15px;
  }
  
  .sidebar-nav {
    width: 100%;
    right: -100%;
  }
  
  .sidebar-nav.active {
    right: 0;
  }
  
  .mobile-title-cn {
    font-size: 15px;
  }
  
  .mobile-title-en {
    font-size: 9px;
  }
  
  .mobile-logo img {
    width: 38px;
    height: 38px;
    margin-right: 10px;
  }
  
  .mobile-logo {
    padding: 6px 10px;
  }
  
  .hamburger-menu {
    width: 36px;
    height: 36px;
    padding: 6px 5px;
  }
}

/* 极小屏幕优化 */
@media screen and (max-width: 360px) {
  .header-mobile {
    height: 55px;
    padding: 0 12px;
  }
  
  .mobile-title-cn {
    font-size: 14px;
  }
  
  .mobile-title-en {
    font-size: 8px;
  }
  
  .mobile-logo img {
    width: 35px;
    height: 35px;
    margin-right: 8px;
  }
  
  .mobile-logo {
    padding: 5px 8px;
  }
  
  .hamburger-menu {
    width: 34px;
    height: 34px;
    padding: 5px 4px;
  }
}

#header .container,
#header .container-fluid {
  padding-left: 0 !important;
  margin-left: 0 !important;
}
</style>



